body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(31, 55, 86);
  background-color: radial-gradient(
    circle,
    rgba(31, 55, 86, 1) 15%,
    rgba(20, 21, 57, 1) 100%
  );
  color: #fff;
}
.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 3px solid hsl(217, 16%, 45%);
  border-radius: 10px;
  width: 60%;
  height: 95px;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: 10%;
}
.center {
  margin-top: -55px;
}
h2 {
  margin-bottom: -33px;
}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
}
.scores {
  width: 75px;
  height: 75px;
  background: #fff;
  border-radius: 7px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.score {
  color: hsl(229, 64%, 46%);
  font-size: 16px;
  font-weight: 700;
  text-transform: uppercase;
}
.num {
  color: hsl(229, 25%, 31%);
  font-size: 35px;
  font-weight: 700;
  margin-top: -5px;
}
.middle {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  height: 350px;
  width: 400px;
  margin: 10% auto;
  position: relative;
}
.wrapper {
  height: 120px;
  width: 120px;
  display: inline-flex;
  border-radius: 50%;
  background-color: #fff;
  align-items: center;
  justify-content: center;
  box-shadow: inset 0 -5px rgba(0, 0, 0, 0.15);
}
.btn-circle {
  border-radius: 50%;
  height: 110px;
  width: 110px;
  border: none;
  cursor: pointer;
  margin: 25px;
  box-shadow: inset 0 -5px rgba(0, 0, 0, 0.15);
}
.btn-circle.btn-paper {
  background: linear-gradient(
    to bottom,
    hsl(230, 89%, 62%),
    hsl(230, 89%, 65%)
  );
}

.btn-circle.btn-scissors {
  background: linear-gradient(to bottom, hsl(39, 89%, 49%), hsl(40, 84%, 53%));
}

.btn-circle.btn-rock {
  background: linear-gradient(
    to bottom,
    hsl(349, 71%, 52%),
    hsl(349, 70%, 56%)
  );
  margin-top: -15px;
}
img {
  background-color: #fff;
  width: 50px;
  height: 50px;
  padding: 1.2rem;
  border-radius: 50%;
}
button {
  border: none;
  padding: 1rem;
  border-radius: 10px;
  margin-left: 5px;
  cursor: pointer;
}
